.photos{
    padding-top: 50px;
    padding-bottom: 50px;
    display: flex;
    flex-direction: column;
    gap: 40px;
    align-items: center;
    
}
.photo:hover{
    border-radius: 30px;
    transition: all 0.3s;
    
}
.photo{
    width: 400px;

}
.h1{
    display: flex;
    justify-content: center;
  
}
.homeImgBlock{
    display: flex;
    justify-content: center; 
}
.homeImg{
    width: 800px;
    border-radius: 50%;
}