.contact {
  
    display: flex;
    flex-direction: row;
    border: 2px double #778794;
    width: 320px;
    margin: 10px;
    padding: 5px;
    border-radius: 10px;
    background-color: #bcc8d1;
    
    
    
 }
 .contact:hover{
   background-color: brown;
 }
 .name {
   min-width: 100px;
 }
 .number {
   justify-content: center;
 }
 
 .input {
   background-color: rgb(153, 196, 196);
   color: #333;
   font-size: 1.2rem;
   padding: 5px;
   border-radius: 0.2rem;
   
   border: none;
   width: 350px;
   border-bottom: 0.3rem solid transparent;
   transition: all 0.3s;
   
 }
 .app{
   width: 300px;
   min-height: 200px;
   margin-left: auto;
   margin-right: auto;
   padding-top: 20px;
   
 
 }
 .icon{
   width: 50px;
   padding: 5px;
 }